.button {
    --width: 100px;
    --height: 35px;
    --tooltip-height: 35px;
    --tooltip-width: 90px;
    --gap-between-tooltip-to-button: 18px;
    --button-color: #01590F;
    --tooltip-color: #fff;
    border-radius: 0.45em;
    width: var(--width);
    height: var(--height);
    background: var(--button-color);
    position: relative;
    text-align: center;
    /* font-family: "Arial"; */
    transition: background 0.3s;
}


.text {
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-wrapper,
.text,
.icon {
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    color: #fff;
}

.text {
    top: 0
}

.text,
.icon {
    transition: top 0.5s;
}

.icon {
    color: #fff;
    top: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button:hover {
    background: #00000000;
}

.button:hover .text {
    top: -100%;
}

.button:hover .icon {
    top: 0;
}

.scrollbar {
    width: 29vw;
    height: 160px;
    margin: 1px;
    overflow-y: scroll;
}

#scrollbar-custom::-webkit-scrollbar {
    width: 14px;
}

#scrollbar-custom::-webkit-scrollbar-track {
    background: #2f3d31;
    border-radius: 10px;
}

#scrollbar-custom::-webkit-scrollbar-thumb {
    background: #26D908;
    border: 3px solid #2f3d31;
    border-radius: 10px;

}

.scrollbar_certificate {
    width: 65vw;
    overflow-x: clip;
    height: 230px;
    margin: 1px;
    overflow-y: scroll;
}

